<template>
  <div class="bars flex ac">
    <div
      class="bar-item text-center item"
      :class="{active: active === index}"
      v-for="(item, index) in list"
      :key="index"
      @click="barChange(index)"
    >{{item.name}}</div>
  </div>
</template>

<script>
export default {
  props: {
    list: { // {name: '', value: ''}
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      active: 0
    };
  },
  methods: {
    barChange(index) {
      if (this.active === index) {
        return
      }
      this.active = index;
      this.$emit('change', index, this.list[index].value)
    }
  }
};
</script>

<style lang="less" scoped>
.bar-item {
  // width: 117px;
  height: 38px;
  line-height: 38px;
  color: #888;
  font-size: 14px;
  background: rgba(255, 255, 255, 1);
  border: 1px solid rgba(212, 212, 212, 1);
  border-right: none;
  &:last-child {
    border-right: 1px solid rgba(212, 212, 212, 1);
  }
  &.active {
    border-color: #f6d9b4;
    color: #e7bd79;
    background:rgba(254,250,243,1);
    position: relative;
    &:after{
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      background:rgba(254,250,243,1);
      border-right: 1px solid #f6d9b4;
      border-bottom: 1px solid #f6d9b4;
      position: absolute;
      bottom: -6px;
      left: 50%;
      transform: translateX(-50%) rotate(45deg);
      z-index: 1;
    }
  }
}
</style>