<template>
  <div>
    <Search placeholder="输入关键词搜索订单" disabled @click.native="toSearch" />
    <Tab :tabs="tabs" lineWidth="1.41rem" @tabChange="tabChange"></Tab>
    <div class="list-container">
      <MenuBar class="menus" :list="menus" @change="menuChange"></MenuBar>
      <div class="list">
        <InfiniteList
          ref="list"
          v-slot="slotProps"
          api="teamManage/orderManage"
          :params="{type, status}"
          immediateCheck
        >
          <div
            v-for="item in slotProps.list"
            :key="item.order_id"
            class="list-item"
          >
            <div class="list-item-title flex ac js">
              <span>订单编号：{{item.order_sn}}</span>
              <span>{{item.status}}</span>
            </div>
            <div
              v-for="item2 in item.order_goods"
              :key="item2.order_id"
              class="list-item-goods flex ac js"
            >
              <CustomImg :src="item2.goods.original_img" width="2.13rem" height="2.13rem"></CustomImg>
              <div class="goos-detail item flex column js">
                <div class="detail-top">
                  <div>{{item2.goods_name}}</div>
                  <div v-if="item2.spec_key_name">{{item2.spec_key_name}}</div>
                </div>
                <div class="flex ac js detail-bottom">
                  <span>金额：￥ {{item2.final_price}}</span>
                  <span>x{{item2.goods_num}}</span>
                </div>
              </div>
            </div>
            <div class="order-info-box">
              <div class="order-info">订单金额：{{item.order_amount}}</div>
              <div class="order-info">支付时间：{{item.pay_time}}</div>
              <div class="order-info">收货人：{{item.consignee}}</div>
              <div class="order-info">收货手机号：{{item.mobile}}</div>
              <div class="order-info">下单人昵称：{{item.users.nickname}}</div>
              <div
                class="btn text-center"
                @click.stop="toOther(`/order/logistics?order_id=${item.order_id}&order_sn=${item.order_sn}`, item.order_id)"
              >查看物流</div>
            </div>
          </div>
        </InfiniteList>
      </div>
    </div>
  </div>
</template>

<script>
import { getLogisticsInfo } from "@/api";
import Search from "@/components/Search";
import Tab from "@/components/Tabs";
import MenuBar from "@/components/MenuBar";
import InfiniteList from "@/components/InfiniteList";

export default {
  components: { Search, Tab, MenuBar, InfiniteList },
  data() {
    return {
      tabs: [
        { name: "进行中", value: 0 },
        { name: "已完成", value: 1 },
        { name: "已关闭", value: 2 }
      ],
      menus: [
        { name: "我的订单", value: 0 },
        { name: "下级订单", value: 1 }
      ],
      type: 0, // 我的订单0 下级订单1
      status: 0 // 进行中0 已完成1 已关闭2
    };
  },
  methods: {
    // 搜索页
    toSearch() {
      this.$router.push("/order/search");
    },
    // tabChange
    tabChange(index, value) {
      this.status = value;
      this.$refs.list.resetData();
    },
    // menuChange
    menuChange(index, value) {
      this.type = value;
      this.$refs.list.resetData();
    },
    // 跳转
    async toOther(path, order_id) {
      if (path.indexOf("logistics") !== -1) {
        // 物流
        const result = await getLogisticsInfo({ order_id });
        if (!result.success) {
          return;
        }
      }
      this.$router.push(path);
    }
  }
};
</script>

<style lang="less" scoped>
.list-container {
  background: #fff;
  overflow: hidden;
  margin-top: 11px;
}

.menus {
  margin: 10px 13px 20px;
}

.list {
  background: #f5f5f5;
}

.list-item {
  margin-bottom: 15px;
  background: #fff;
  padding: 0 13px;
  overflow: hidden;
}

.list-item-title {
  margin: 10px 0 5px;
}

.list-item-title {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  span:last-child {
    color: #e84114;
  }
}

.list-item-goods {
  padding: 15px 0;
  border-bottom: 1px solid #d8d8d8;
}

.goos-detail {
  height: 80px;
  padding-left: 10px;
}

.detail-top {
  height: 0;
  div:first-child {
    color: #282828;
    font-size: 14px;
    line-height: 20px;
  }
  div:nth-child(2) {
    height: 16px;
    font-size: 10px;
    line-height: 16px;
    color: #666;
    padding: 0 5px;
    display: inline-block;
    background: rgba(246, 247, 248, 1);
    vertical-align: top;
    margin-top: 3px;
    border-radius: 5px;
  }
}

.detail-bottom {
  color: #979797;
  font-size: 14px;
}

.order-info-box {
  padding: 5px 0;
  overflow: hidden;
  position: relative;
}

.order-info {
  color: #282828;
  font-size: 14px;
  line-height: 20px;
  margin: 10px 0;
}

.btn {
  box-sizing: border-box;
  width: 80px;
  padding: 3px 0;
  border-radius: 17px;
  border: 1px solid rgba(40, 40, 40, 1);
  color: #282828;
  font-size: 14px;
  position: absolute;
  top: 20px;
  right: 0;
}
</style>




