<template>
  <van-search
    class="custom-search"
    v-model="value"
    :placeholder="placeholder"
    :clearable="clearable"
    :autofocus="autofocus"
    :background="background"
    :disabled='disabled'
    :left-icon="require('@/assets/search2.png')"
    @input='debounceInput'
  />
</template>

<script>
import Vue from "vue";
import debounce from 'lodash/debounce'
import { Search } from "vant";

Vue.use(Search);

export default {
  props: {
    placeholder: {
      type: String,
      default: "请输入搜索关键词"
    },
    clearable: {
      type: Boolean,
      default: true
    },
    autofocus: {
      // ios不支持
      type: Boolean,
      default: true
    },
    background: {
      type: String,
      default: "#f5f5f5"
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      value: ""
    };
  },
  created() {
    this.debounceInput = debounce(this.onInput, 500)
  },
  methods: {
    onInput(value) {
      if (!value) {
        return
      }
      this.$emit('search', value)
    }
  },
};
</script>

<style lang="less">
.custom-search {
  .van-search__content {
    background: #fff;
  }
}
</style>